import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';

/**
 * Component này dùng chung cho Hóa đơn điẹn tử -> 4 sàn -> Hóa đơn đã phát hành, Hóa đơn trả về
 */
@Component({
  selector: 'app-sales-return-invoice',
  templateUrl: './sales-return-invoice.component.html',
  styleUrls: ['./sales-return-invoice.component.scss']
})
export class SalesReturnInvoiceComponent {
  public date: Date[] = [new Date(), new Date()];
  public searchValue: string = ''
  public invoiceShow: any[] = []
  private invoiceSegment: any[] = []
  private invoiceFilter: any[] = []
  public invoices: any[];

  tableHeight: string;
  status: any;
  totalValInvoice: number = 0;
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  listTabs = ['Sold', 'Returned']
  customers = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3);

  /** id_shop để lát nữa query hiện lên danh sách */
  id_shop: string = 'local';
  /* Loại hóa đơn sẽ hiển thị */
  bill_types: number[] = [1, 3, 4, 23];
  /** Có phải được router đến từ Invoice hay không */
  get isFromInvoice(): boolean {
    return !this.dataRestore || !this.dataRestore.shop_info;
  }



  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    public vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private fncService: FunctionService
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59);
  }

  

  ngAfterContentInit(): void {
   
    if (this.dataRestore) {
      // Lấy id_shop
      this.id_shop = this.dataRestore?.shop_info?.id_shop ?? this.id_shop;
      // Lấy bill_type
      this.bill_types = this.dataRestore?.shop_info?.bill_type ?? this.bill_types;
      // filter tab 1 đã bán - 3 trả về
      if(this.id_shop && this.id_shop != 'local' && this.bill_types) {
        if(!this.bill_types.includes(1)) this.listTabs = this.listTabs.filter(item => item != 'Sold');
        if(!this.bill_types.includes(3)) this.listTabs = this.listTabs.filter(item => item != 'Returned');
      }
      
      this.searchValue = this.dataRestore.searchValue ?? this.searchValue;
      this.filterType = this.dataRestore.filterType ?? this.filterType;
      
      
      this.date = [this.dataRestore.startTime ?? this.date[0], this.dataRestore.endTime ?? this.date[1]];
      this.selectedIndex = this.dataRestore.selectedIndex ?? this.selectedIndex;

      this.invoices = this.dataRestore.invoice ?? [];
      this.segmentChanged(this.selectedIndex);
      if (this.dataRestore.reload) this.getInvoices();
    }
    else { this.filterType = 'all'; }
  }

  /**
   * get danh sách hóa đơn bán hàng từ DB
   * @example this.getInvoiceToday(1)
   */
  getInvoices() {
    // if (this.vhAuth.checkMyPermission("sell_enable_view_bill")) {
      this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => {
        this.vhQuerySales.getBills_byFields( { date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $in: this.bill_types }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id }, id_shop: { $eq: this.id_shop } }, {})
          .then((invoice: any[]) => {
            console.log(invoice)
            this.invoices = invoice.map(item => {
              return {
                ...item,
                partner_name: this.renderPartner(item.id_customer),
                payment_name: this.renderPayment(item.payment_type, item.id_wallet),
                status: this.renderStatus(item.bill_type),
                name_employee: this.vhAuth.getlocalEmployee(item.id_employee).name,
                debt_value: this.getDebtValue(item.total, item.tax, item.payment),
                sum_price: item.total * (1 + item.tax / 100)
              }
            })
            
            this.segmentChanged(this.selectedIndex);
            this.status = "Not found"
            this.vhComponent.hideLoading(0);
          }, err => {
            this.vhComponent.hideLoading(0);
          }).catch(err => this.vhComponent.hideLoading(0))
      })
    // }
    // else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")

  }


  /**
   * trả về công nợ của hóa đơn
   * @example let debt = this.getDebtValue(100000,0,0)
   */
  getDebtValue(total, tax, payment) {
    return (parseFloat(parseFloat((total * (1 + tax / 100)).toString()).toFixed(0)) - payment)
  }

  /**
   * trả về tên loại trạng thái bill
   * @param bill_type 
   * @returns string
   * @example let bill_name = this.renderStatus(2)
   */
  renderStatus(bill_type: 1 | 3 | 4 | 23) {
    switch (bill_type) {
      case 4: return `${this.lang.translate('Cancelled')} (${this.lang.translate('Sold')})`
      case 23: return `${this.lang.translate('Cancelled')} (${this.lang.translate('Returned')})`
      case 1: return this.lang.translate('Sold')
      default: return this.lang.translate('Returned')
    }
  }
  /**
   * trả về tên kh của bill
   * @param id_partner 
   * @returns string
   * @example let customer_name = this.renderPartner('DNIKWUJHNBDUYWGU') 
   */
  renderPartner(id_partner: string) {
    let customer: any = this.vhQuerySales.getlocalCustomer(id_partner)
    return customer ? this.lang.translate(customer.name) : this.lang.translate("Not found")
  }
  /**
   * router qua trang chi tiết bill
   * @example this.goToDetail('DMNWIDHNBIUWHGDUWY')
   */
  goToDetail(data) {
    let dataRestore: any = { 
      searchValue: this.searchValue, 
      filterType: this.filterType, 
      startTime: this.date[0], 
      endTime: this.date[1], 
      selectedIndex: this.selectedIndex, 
      invoice: this.invoices, 
    }
    if(this.isFromInvoice) {
      if(data.bill_type == 3) this.router.navigate(["/sales/dashboard/manage/invoice/sales-return-invoice/detail-customer-return"], { state: { id: data._id, dataRestore } })
      if(data.bill_type == 1) this.router.navigate(["/sales/dashboard/manage/invoice/sales-return-invoice/detail-sales"], { state: { id: data._id, dataRestore } })
      }
    else {
      dataRestore.shop_info = this.dataRestore.shop_info;
      this.router.navigate(["/sales/dashboard/manage/invoice/sales-return-invoice/detail-sales"], { state: { id: data._id, dataRestore } })
    }
  }
  public filterType = 'all';
   
  /**
   * lọc hóa đơn theo loại hóa đơn
   * @example this.sortFilter(1)
   */
  sortFilter(filterType) {
    if (filterType == 'all') {
      this.invoiceFilter = this.invoiceSegment
    }
    else {
      this.invoiceFilter = this.invoiceSegment.filter(item => item.id_customer == filterType)
    }
    this.searchBill(this.searchValue)
  }
  /**
   * lọc bill theo ['bill_code', 'partner_name', 'payment_name', 'status', 'name_employee']
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceFilter, ['bill_code', 'partner_name', 'payment_name', 'name_employee', 'status'])
    } else this.invoiceShow = [...this.invoiceFilter]

    this.totalValInvoice = this.getTotal()
    
  }
  /**
   * trả về tổng daonh số của tất cả hóa đơn đang hiển thị
   * @returns 
   */
  getTotal() {
    return this.invoiceShow.reduce((prev: number, next) => prev + next.total * (1 + next.tax / 100), 0);
  }


  ngAfterViewChecked() {
    if (document.querySelector("#sales-return-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-tabs-nav") && document.querySelector(".invoice-header")) {
      this.tableHeight = document.querySelector("#sales-return-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".invoice-header").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - 60 + "px";
      if (document.querySelector(".ant-table-pagination")) {
        this.tableHeight = document.querySelector("#sales-return-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
          - document.querySelector(".invoice-header").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
      }
    }

    this.cdRef.detectChanges();
  }
  /**
   * trả về tên phương thức thanh toán của đơn
   * @example let payment_name= this.renderPayment(1)
   */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.lang.translate('Cash')
      case 2: return this.lang.translate('Debit')
      case 3: return this.vhQuerySales.getlocalWallet(id_wallet).name
    }
  }
  goBack() {
    // Nếu từ hóa đơn bán khách trả
    if(this.isFromInvoice) {
      this.router.navigate(['/sales/dashboard/manage/invoice']);
    }
    // Nếu từ Thương mại điện tử
    else {
      let url = '';
      if(!this.isFromInvoice) {
        // Xử lý router từ Thương mại điện tử
        const fromType = this.dataRestore?.shop_info?.from_type;
        if (fromType?.includes('invoice_issued')) {
          const platform = fromType.replace('_invoice_issued', '');
          url = `/sales/dashboard/manage/ecommerce/${platform}/issue-invoice`;
        } 
        else if (fromType?.includes('invoice_returned')) {
          const platform = fromType.replace('_invoice_returned', '');
          url = `/sales/dashboard/manage/ecommerce/${platform}/issue-invoice`;
        }
      }
      this.router.navigate([url]);
    }
  }
  selectedIndex: number = 0;
  mess_result = ''
  /**
   * bắt sk thay đổi tab loại hóa đơn
   * @param value 
   */
  segmentChanged(value) {
    if (this.invoices) {
      if(this.listTabs[value] == 'Returned') {
        value = 1; // Trả về hóa đơn trả về
      }
      switch (value) {
        case 0: {
          if (this.vhAuth.checkMyPermission("sell_enable_view_bill")) { this.invoiceSegment = this.invoices.filter(item => item.bill_type == 1); this.mess_result = 'Not invoice yet'; }
          else { this.invoiceSegment = []; this.mess_result = 'You do not have this rights'; }
          break;
        }
        case 1: {
          if (this.vhAuth.checkMyPermission("customer_return_enable_view_bill")) { this.invoiceSegment = this.invoices.filter(item => item.bill_type == 3); this.mess_result = 'Not invoice yet'; }
          else { this.invoiceSegment = []; this.mess_result = 'You do not have this rights'; }
          break;
        }
        // case 2: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 5); break;
        // case 2: 
        // this.invoiceSegment = this.invoices.filter(item => item.bill_type == 4); break;
        // case 3: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 23); break;
      }
      this.sortFilter(this.filterType);
    }
  }

  /**
   * bắt sk thay đổi thời gian xem hóa đơn
   * @param event array<date>
   * @example this.changeTime([2023-06-23T07:12:35.157Z,2023-06-23T07:12:35.157Z])
   */
  changeTime(event) {
    if (new Date(event[1].setHours(0, 0, 0, 0)).getTime() - new Date(event[0].setHours(0, 0, 0, 0)).getTime() <= this.fncService.checkDateReport() * 86000000) {
      //this.getTransferList();
    } else {
      this.vhComponent.alertMessageDesktop("error", this.lang.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " + this.lang.translate("days"))
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
    }
  }

  /**
   * lưu mã đơn vào RAM
   * @example this.copyBillCode('230623A001')
   */
  copyBillCode(billCode) {
    this.vhComponent.copyValue(billCode)
  }
  /**
   * format lại định dạng hiển thị ngày tháng để tải về file excel
   * @param date 
   * @param type 
   * @returns string
   * @example let date = this.format_date(2023-06-23T07:12:35.157Z, 'dd/MM/yyyy')
   */
  format_date(date, type) {
    return this.datePipe.transform(date, type)
  }
  /**
   * trả về tên loại bill để tạo tên file excel
   * @param selectedIndex 
   * @returns string
   * @example let bill_name = this.renderNameExcel(0)
   */
  renderNameExcel(selectedIndex) {
    switch (selectedIndex) {
      case 0: return `${this.lang.translate('Sales invoice')} (${this.lang.translate('Sold')})`
      case 1: return `${this.lang.translate('Customer returned invoice')} (${this.lang.translate('Returned')})`
      case 2: return `${this.lang.translate('Sales invoice')} (${this.lang.translate('Cancelled sales')})`
      case 3: return `${this.lang.translate('Customer returned invoice')} (${this.lang.translate('Cancelled return')})`
    }

  }
  //xuất excel hóa đơn
  export() {
    let data = [];
    let name = `${this.renderNameExcel(this.selectedIndex)}_${this.lang.translate("_from")}_${this.format_date(this.date[0], 'dd_MM_yyyy')}_${this.lang.translate("_to")}_${this.format_date(this.date[0], 'dd_MM_yyyy')}_${this.vhAlgorithm.changeAlias(this.vhQuerySales.getDefaultBranch().name).replace(/\s/g, "_")}`;
    let total: any = new Object();
    total[this.lang.translate('Date')] = ''
    total[this.lang.translate('Invoice code')] = this.lang.translate('Total')
    total[this.lang.translate('Customer')] = ''
    total[this.lang.translate('Payment method')] = ''
    total[this.lang.translate('Employee')] = ''
    total[this.lang.translate('Note')] = ''
    total[this.lang.translate('Total')] = this.vhAlgorithm.vhcurrencyunit(this.totalValInvoice)
    data.push(total)
    for (let bill of this.invoiceShow) {
      let item: any = new Object();
      item[this.lang.translate('Date')] = this.format_date(bill.date, 'dd/MM/yyyy HH:mm')
      item[this.lang.translate('Invoice code')] = bill.bill_code
      item[this.lang.translate('Customer')] = bill.partner_name
      item[this.lang.translate('Payment method')] = `${bill.payment_name} ${(bill.debt_value && bill.payment_type != 2) ? `- ${this.lang.translate('Rest')} ${this.vhAlgorithm.vhcurrencyunit(bill.debt_value)}` : ''}`
      item[this.lang.translate('Employee')] = bill.name_employee
      item[this.lang.translate('Note')] = bill.note
      item[this.lang.translate('Total')] = this.vhAlgorithm.vhcurrencyunit(bill.total * (1 + bill.tax / 100))
      data.push(item);
    }
    this.vhAlgorithm.exportXLSX(data, name)
  }


  /** hàm sort cho các cột */
  dateCol = false;
  bill_codeCol = false;
  partner_nameCol = false;
  payment_nameCol = false;
  name_employeeCol = false;
  sum_priceCol = false;
  /** Hàm thực hiện sắp xếp theo collection
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName) {
    switch (colName) {
      case 'date':
        if (this.dateCol) {
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if (this.bill_codeCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'partner_name':
        if (this.partner_nameCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'payment_name':
        if (this.payment_nameCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'name_employee':
        if (this.name_employeeCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'sum_price':
        if (this.sum_priceCol) {
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }
  /**
   * lọc khách hàng theo tên
   * @param value 
   */
  searchFilter(value) {
    this.customers = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3), ['name'])
  }
}
