import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SalesReturnInvoiceComponent } from './sales-return-invoice.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core'; 
import { CanLeavePage } from 'src/app/canLeavePage';
import { SalesDesktopBillType1Module } from 'src/app/sales/components/bill-type/bill-type-1/sales-desktop/sales-desktop-bill-type1.module';
import { BillType3Module } from 'src/app/sales/components/bill-type/bill-type-3/mange-invoice/bill-type3.module';
import { DetailCustomerReturnComponent } from './detail-customer-return/detail-customer-return.component';
import { DetailSalesComponent } from './detail-sales/detail-sales.component';
import { EditCustomerReturnComponent } from './edit-customer-return/edit-customer-return.component';
import { EditSalesComponent } from './edit-sales/edit-sales.component';
 
const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: "",
        component: SalesReturnInvoiceComponent
      },
      {
        path: "detail-customer-return",
        component: DetailCustomerReturnComponent
      },
      {
        path: "edit-customer-return",
        component: EditCustomerReturnComponent
      },
      {
        path: "detail-sales",
        component: DetailSalesComponent
      },
      {
        path: "edit-sales",
        component: EditSalesComponent
      },
    ]
  },

]

@NgModule({
  declarations: [SalesReturnInvoiceComponent, DetailCustomerReturnComponent, DetailSalesComponent, EditCustomerReturnComponent, EditSalesComponent, ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    SalesDesktopBillType1Module,
    BillType3Module
     
  ]
})
export class SalesReturnInvoiceModule { }
